/* dashboard.css */
.dashboardContainer {
  display: flex;
  flex-direction: column;
}

.dashboardContent {
  display: flex;
  flex: 1;
  overflow: hidden;
}

.mainContent {
  flex: 1;
  padding: 1rem;
  overflow-y: auto;
  background-color: #f9f9f9;
}

/* Sidebar styles */
.sidebar {
  width: 250px;
  background-color: #333;
  color: white;
  padding: 1rem;
}

/* Navbar styles */
.navbar {
  background-color: #1a73e8;
  color: white;
  padding: 1rem;
}

  