/* Sidebar styles */
aside {
  background-color: #f8f9fa;
  width: 250px; /* Adjust as needed */
  height: 100vh;
  position: fixed;
  top: 60px; /* Adjust based on your navbar height */
  left: 0;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  border-radius: 0 0 10px 10px; /* Rounded bottom corners */
  display: flex;
  flex-direction: column;
  transition: transform 0.3s ease, width 0.3s ease; /* Smooth transition for toggling */
}

/* Hide sidebar on small screens */
@media (max-width: 768px) {
  aside {
    width: 200px; /* Adjust as needed */
    transform: translateX(-100%); /* Hide off-screen */
  }

  aside.show {
    transform: translateX(0); /* Show when toggled */
  }
}

/* Sidebar link styles */
aside a {
  color: #333;
  text-decoration: none;
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px; /* Rounded corners for links */
  font-size: 1.1rem; /* Slightly larger font size */
  font-weight: bold; /* Bold text */
  transition: background-color 0.3s, color 0.3s, padding 0.3s; /* Smooth transitions */
}

/* Link hover effect */
aside a:hover {
  background-color: #007bff;
  color: #fff;
  padding-left: 20px; /* Indent on hover */
}

/* Active link style */
aside a.active {
  background-color: #0056b3;
  color: #fff;
  padding-left: 20px; /* Indent for active links */
}

/* Toggle button styles */
.toggle-button {
  display: none; /* Hidden by default */
}

@media (max-width: 768px) {
  .toggle-button {
    display: block;
    position: fixed;
    top: 20px;
    left: 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 50%;
    padding: 10px 15px;
    cursor: pointer;
    z-index: 1000; /* Ensure it's above other content */
    font-size: 1.2rem; /* Larger font size for the button */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow for the button */
  }
}
