.container {
    max-width: 800px;
    margin: 0 auto;
    padding: 10px;
    text-align: center;
  }
  
  h1 {
    font-size: 2.5rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  p {
    font-size: 1.25rem;
    color: #666;
    line-height: 1.6;
  }
  
  .container p {
    margin-bottom: 15px;
  }
  
  @media (max-width: 768px) {
    .container {
      padding: 15px; 
    }
  
    h1 {
      font-size: 2rem;
    }
  
    p {
      font-size: 1.1rem;
    }
  }
  
  /* Mobile devices */
  @media (max-width: 480px) {
    .container {
      padding: 20px; 
    }
  
    h1 {
      font-size: 1.75rem; 
    }
  
    p {
      font-size: 1rem; 
      line-height: 1.5;
    }
  }  