/* navbar.module.css */
.containerNavbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  height: 60px;
  background: linear-gradient(90deg, #1e3c72, #2a5298, #000000); /* Gradient background */
}

.navbarTitle {
  font-weight: bold;
  color: #fff; /* White color for title */
  font-size: 34px;
  font-family: Arial, sans-serif;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.nav {
  display: flex;
  justify-content: flex-end;
  gap: 15px;
}

.link {
    color: white;
    text-decoration: none;
    font-size: 18px;
    padding: 10px 20px;
    background-color: #007bff;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    text-align: center;/* Smooth color transition on hover */
}

.link:hover {
  color: #ddd; /* Lighter shade on hover */
  text-decoration: underline;
}

@media (max-width: 768px) {
  .containerNavbar {
    flex-direction: column;
    align-items: flex-start;
    height: auto;
  }

  .nav {
    justify-content: center;
    width: 100%;
    gap: 10px;
  }

  .navbarTitle {
    margin-bottom: 10px;
    text-align: center;
  }
}
